<template>
	<li>
		<i :class="iconClass" class="fa fa-fw" aria-hidden="true"></i>
		<a :href="targetId">{{ name }}</a>
	</li>
</template>

<script>
	export default {
		name: "SidebarItem",
		props: [
			"name",
			"iconClass",
			"targetId"
		],
		data () {
			return {}
		}
	}
</script>

<style lang="scss" scoped>
	li {
		width: 50px;
		height: 50px;
		line-height: 50px;
		&:hover {
			& a {
				left: 0px;
			}
		}
		& i {
			position: absolute;
			width: 50px;
			height: 50px;
			line-height: 50px;
			font-size: 25px;
		}
		& a {
			background-color: white;
			text-align: left;
			display: block;
			position: absolute;
			left: -150px;
			width: 100px;
			height: 50px;
			font-family: 'Barlow Condensed', sans-serif;
			font-size: 1.5em;
			text-decoration: none;
			padding: 0px 25px 0px 25px;
			transition: all 0.1s ease-in;
		}
	}
</style>


