	<div>
		<sidebar/>
		<title-section/>
		<experiences-section/>
		<projects-section/>
		<contact-me-section/>
	</div>
</template>

<script>
	import Sidebar from './sidebar/Sidebar'
	import TitleSection from './landingPageSections/TitleSection'
	//import ProjectsSection from './landingPageSections/ProjectsSection'
	import ExperiencesSection from './landingPageSections/ExperiencesSection'
	//import ContactMeSection from './landingPageSections/ContactMeSection'
	export default {
		name: "LandingPage",
		components: {
			Sidebar,
			TitleSection,
			//ProjectsSection,
			ExperiencesSection,
			//ContactMeSection
		}
	}
</script>

<style lang="scss">
	.section {
		display: flex;
		justify-content: center;
		& .section_content {
			width: 66%;
		}
