<template>
	<div id="experiences_section" class="section">
		<div class="section_content">
			<header class="section_header">
				<span class="title_container">
					<span class="title">Experiences</span>
					<hr>
				</span>
			</header>
			<section>
				<div class="company_list_container">
					<ul class="company_list">
						<li id="company_list_borealis">
							<a href="https://www.boreal-is.com/fr/">
								<div>
									<img src="../../assets/borealis_md2.png" alt="">
								</div>
							</a>
						</li>
						<li id="company_list_rtings">
							<a href="https://www.rtings.com/">
								<div>
									<img src="../../assets/rtings_md.png" alt="">
								</div>
							</a>
						</li>
						<li id="company_list_metareal">
							<a href="https://www.metareal.com/">
								<div>
									<img src="../../assets/metareal_md.png" alt="">
								</div>
							</a>
						</li>
					</ul>
				</div>
			</section>
		</div>
	</div>
</template>

<script>
	export default {
		name: "LandingPageExperiencesSection"
	}
</script>

<style lang="scss" scoped>
	.section {
		background-color: rgb(15, 15, 15);
	}
	.section_header {
		min-height: 250px;
		display: flex;
		justify-content: center;
		align-items: center;
		& .title_container {
			& .title {
				text-transform: uppercase;
				display: flex;
				justify-content: center;
				font-family: "Barlow Condensed", sans-serif;
				font-size: 3em;
			}
		}
	}
	.company_list_container {
		padding-bottom: 100px;
		& ul.company_list {
			padding: 0px;
			display: flex;
			flex-flow: row wrap;
			list-style: none;
			box-shadow: 0px 0px 7px black;
			& li {
				flex: 1;
				transition: all 0.2s ease;
				background-size: 175% auto;
				min-height: 250px;
				&:hover {
					z-index: 99;
				}
				& div {
					padding-left: 25px;
					padding-right: 25px;
					height: 100%;
					display: flex;
					justify-content: center;
					align-items: center;
					background-color: rgba(0, 0, 0, 0.6);
					transition: all 0.2s ease;
					&:hover {
						box-shadow: 0px 0px 10px black;
						background-color: rgba(0, 0, 0, 0.50);
						transition: all 0.2s ease;
					}
				}
				&#company_list_borealis {
					background-image: url(../../assets/borealis_background.jpg);
				}
				&#company_list_rtings {
					background-image: url(../../assets/rtings_background.jpg);
				}
				&#company_list_metareal {
					background-image: url(../../assets/metareal_background.jpg);
				}
			}
		}
	}
</style>


