<template>
	<div class="sidebar">
		<ul class="sidebarItemList">
				<sidebar-item
					v-bind:key="item"
					v-for="item in items"
					:name="item.name"
					:target-id="item.targetId"
					:icon-class="item.iconClass"
				/>
		</ul>
	</div>
</template>

<script>
	import SidebarItem from './SidebarItem'
	export default {
		name: "Sidebar",
		components: {
			SidebarItem,
		},
		data () {
			return {
				items: [
					{
						name: "Landing",
						targetId: "#title_section",
						iconClass: "fa-user-circle"
					},
					{
						name: "Experiences",
						targetId: "#experiences_section",
						iconClass: "fa-briefcase"
					},
					{
						name: "Projects",
						targetId: "#projects_section",
						iconClass: "fa-terminal"
					},
					{
						name: "Contacts",
						targetId: "#contacts_section",
						iconClass: "fa-envelope"
					}
				]
			}
		}
	}
</script>

<style lang="scss" scoped>
	.sidebar {
		position: fixed;
		width: 50px;
		height: 100%;
		background-color: #0d0d0d;
		box-shadow: 0px 0px 5px black;
	}
	.sidebarItemList {
		padding: 0px;
		margin: 0px;
		margin-top: 15px;
		list-style: none;
	}
</style>


